import React, { useEffect } from 'react';
import { ethers } from "ethers";
import { Web3ReactProvider } from '@web3-react/core';
import { ThemeProvider, Cursor } from '@react95/core';
import styled from 'styled-components';
import '@react95/icons/icons.css';
import wallpaper from './image/wallpaper.jpg';
// import mixpanel from 'mixpanel-browser';
import { HomePage } from './page';
// mixpanel.init("a4ba5649d672ab6964e61d4c4f37bb5e");

function App() {
  // @ts-ignore
  function getLibrary(provider, connector) {
    const library = new ethers.providers.Web3Provider(provider)
    library.pollingInterval = 12000
    return library
  }

  useEffect(() => {
    // @ts-ignore
    if (mixpanel) mixpanel.track('App Loaded');
  });

  return (
    <Web3ReactProvider getLibrary={getLibrary}>
      <ThemeProvider theme="maple">
        <AppStyleOverrides>
          <HomePage />
        </AppStyleOverrides>
      </ThemeProvider>
    </Web3ReactProvider>
  );
}

const AppStyleOverrides = styled.div`
  background: url(${wallpaper});
  background-repeat: repeat;
  background-size: cover !important;
  background-position: center;
  min-height: 100%;
  width: 100%;
  ${Cursor.Auto};

  button, button:hover, label, label:hover, input {
    ${Cursor.Auto};
  }
`;


export default App;
